.products__list {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 15px;
  list-style: none;
  display: flex;
}

.products__item {
  background-color: #f5f4fa;
  border-radius: 15px;
  width: 235px;
  height: 300px;
  padding: 15px;
}

.products__item:last-child .products__img {
  max-width: 140px;
  height: 90px;
  margin-top: 15px;
}

.products__img {
  mix-blend-mode: darken;
  border-radius: 5px;
  max-width: 100px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.products__title {
  text-align: center;
  margin-top: 50px;
  font-size: 20px;
  font-style: italic;
}

.products__text {
  text-align: center;
  letter-spacing: .3px;
  margin-top: 20px;
  font-weight: 600;
}

.products__price {
  text-align: center;
  letter-spacing: .5px;
  font-weight: 500;
}

